@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:ital,wght@1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:ital,wght@1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100vh;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 800% 800%;
  -webkit-animation: gradient 12s ease infinite;
          animation: gradient 12s ease infinite;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}
#iAmA {
  font-family: 'IBM Plex Mono', monospace;
  text-align: center;
  font-size: 20px;
  padding-top: 8px;
}

#titleHead {
  font-family: 'JetBrains Mono', monospace;
  font-weight: 500;
  text-align: center;
  display: block;
  width: 100%;
  color: #2a2a2a;
  letter-spacing: 1px;
  font-size: 38px;
  margin-bottom: 0px;
  margin-top: 80px;
}

#typewriterText {
  font-size: 20px;
  font-weight: 800;
  font-family: 'IBM Plex Mono', monospace;
  color: #000;
  background-image: linear-gradient(-90deg, rgb(131, 255, 73), rgb(131, 255, 73));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#underscore {
  display: inline-block;
  position: relative;
  top: -0.14em;
  left: 5px;
  color: #fff;
  font-size: 22px;
  font-weight: 500;
}
#navbar {
  background: #0e0e0e;
  position: fixed;
  top: 0;
  width: 100%;
  padding-left: 10px;
  padding-top: 4px;
  padding-bottom: 4px;
  /* To make sure it is on top of any other content */
  z-index: 100;
}

#navbar a {
  float: left;
  display: block;
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 7px;
  padding-bottom: 4px;
  border-radius: 4px;
}

#navbar a:hover {
  background-color: rgb(71, 71, 71);
}
#separator {
  width: 80%;
  margin-left: 10%;
  border-bottom: solid 1px rgba(57, 57, 57, 0.4);
  margin-top: 20px;
  margin-bottom: 40px;
}
#langsContainer {
  text-align: center;
  padding-top: 60px;
  font-family: 'Nunito', sans-serif;
  margin: auto;
}

#langsTitle {
  font-size: 20px;
  font-weight: normal;
  font-family: monospace;
}

#langs {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 30px;
}

#highlightLang {
  padding: 5px;
  width: 80px;
  margin: 5px;
}

.langSeparator {
  padding-left: 10px;
  padding-right: 10px;
  font-size: 30px;
  color: transparent;
  /* display: none; */
}
#headingTitle {
  font-size: 22px;
  font-weight: lighter;
  font-family: 'JetBrains Mono', monospace;
  text-align: center;
  color: #2a2a2a;
}
#card {
  max-width: 298px;
  margin-top: 30px;
  font-family: 'Nunito', sans-serif;
}

h2 {
  padding: 20px 0px;
  text-align: center;
}

.card-row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.card {
  width: 298px;
  padding: 20px;
  margin: 10px;
  border-radius: 12px;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
  background-color: rgba(58, 45, 45, 0.2);
  transition: 0.15s;
}

.card:hover {
  transition: 0.1s ease-in-out;
  box-shadow: 2px 4px 8px 0 rgba(0, 0, 0, 0.3);
  background-color: rgba(58, 45, 45, 0.3);
  width: 325px;
}

.card p {
  color: #f8f8f8;
  line-height: 1.5;
  padding-top: 10px;
  white-space: pre-wrap;
  overflow-wrap: break-word;
  text-shadow: 1px 1px #262626;
}

.card a {
  color: rgb(1, 236, 249);
  text-decoration: none;
  border-bottom: solid 1px rgb(79, 79, 79);
  text-shadow: 1px 1px #262626;
  white-space: pre-wrap;
  overflow-wrap: break-word;
}

.card a:hover {
  color: rgb(0, 223, 235);
  text-shadow: 1px 1px #000000;
}

.appName {
  font-size: 24px;
  white-space: pre-wrap;
  overflow-wrap: break-word;
}

.card a.buttonLink {
  display: block;
  border-bottom: none;
  padding: 14px;
  text-align: center;
  background: linear-gradient(-45deg, #23d5ab, #23a6d5, #e73c7e, #ee7752);
  background-size: 600% 600%;
  -webkit-animation: gradient 12s ease infinite;
          animation: gradient 12s ease infinite;
  color: white;
  margin-top: 6px;
  border-radius: 6px;
  position: 0;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.10);
  text-shadow: none;
}

.card a.buttonLink:hover {
  transition: 0.2s ease-in-out;
  -webkit-animation: gradient 2s ease infinite;
          animation: gradient 2s ease infinite;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.20);
}

.scrolling-wrapper {
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
  margin-left: 2%;
  margin-right: 2%;
}

#card {
  display: inline-block;
  margin-right: 30px;
}

.card .cardDate {
  color: #5b5c5c;
  text-shadow: 1px 1px #000000;
}

.scrolling-wrapper::-webkit-scrollbar {
  display: none;
}
